          <!--          <li v-for="(item,index) in dialogData.loginGame_child " style="    direction: rtl;">-->
          <!--            {{item.count}} : {{item.description}}-->
          <!--          </li>-->
        </ul>
        <span class="dialog-footer" slot="footer">
